 /* Pending Users
--------------------------------*/
.pendingUsers{
    height: 75%;
    width: 100;
    /* background-color: palegreen; */
    overflow: auto;
  }
  
  .pendingUsers .users li{
    background-color: rgba(0, 0, 255, 0.048);
    border-color: #ddd;
    color: #333;
    text-shadow: 0 1px 0 #f3f3f3;
    border-top-left-radius: 0.1em;
    border-width: .0px 0 0;
    border-style: solid;
    overflow: visible;
    width: 100%;
  
  }
  
  .pendingUsers .users li > a{
    display: flex;
    flex-direction: row;
    background-color: #f6f6f649;
    border-color: #ddd;
    color: #333;
    height: 100%;
    text-shadow: 0 1px 0 #f3f3f3;
    border-top-left-radius: 0.1em;
    border-width: 5px 0 0;
    text-decoration: none;
    height: auto;
  
  
   
  }
  
  .pendingUsers .users li > a:hover{
    /* background-color: rgba(0, 0, 255, 0.151);
    border-color: #ddd;
    color: #333;
    text-shadow: 0 1px 0 #f3f3f3; */
  }
  
  .pendingUsers .users .profile-img{
    width: 25%;
    /* border-radius: 50%; */
    overflow: hidden;
    padding: 0.5em 0 0.5em 0;
    margin-left: 0.5em;
  
  }
  
  .pendingUsers .users .contact-info{
    width: 50%;
    overflow-wrap: break-word;
    word-wrap: break-word;
  
  
  }
  
  .pendingUsers   .users .contact-info div {
    margin: 0;
    display: flex;
    align-items: center;
    height: 100%;
    padding-left: 0.3em;
    overflow-wrap: break-word;
    word-wrap: break-word;
    word-break: break-word;
    width: 100%;
    font-size: 1.1em;
    font-weight: 600;
    color:white;
  }
  .pendingUsers   .users .pending-user-control {
    flex-grow: 1;
  }
  
  .profile-img img {
    width: 100%;
    height:100%;
  }
  
  .pendingUsers ul {
    width: 100%;
    height: 100%;
    list-style-type: none;
    padding: 0%;
    margin: 0%;
  }
  .pendingUsers ul>:first-child{
      
  }
  
  
  /* //Reject Add Pending User */
  .pending-user-control{
    position: relative;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
  }
  
  .pending-user-control div {
    flex-grow: 1;
  }
  
  .pending-user-control button {
      
        display: inline-block;
        cursor: pointer;
        color: #ffffff;
        font-family: Arial;
        text-decoration: none;
        text-shadow: 0px 1px 0px #2f6627;
        width: 100%;
        height: 100%;
  }
.pending-user-control .add-contact button {
      background-color:#5cbf2a91;
      border: 1px solid #5cbf2a93;
      
}

.pending-user-control .add-contact button:hover {
  background-color:#5cbf2a;
  border: 1px solid #5cbf2a;
  
}
.pending-user-control .reject-contact button {
    background-color:#bf722a8c;
    border: 1px solid #bf722a93;
}
.pending-user-control .reject-contact button:hover {
  background-color:#bf722af1;
  border: 1px solid #bf722a;
}
  .pending-user-control button:active {
      position:relative;
      top:1px;
  }
  